import React from 'react'
import { Box } from '@mui/material'
import CourseInfo from '../../../components/Sub/DataTable/CourseInfo/CourseInfo'
import StatusSelect from '../../../components/Sub/DataTable/StatusSelect/StatusSelect'
import CompanyInfoAvatar from '../../../components/Sub/DataTable/CompanyInfoAvatar/CompanyInfoAvatar'
import ContactsMailIcon from '../../../assets/ContactsIcon/ContactsIconComponents/ContactsMailIcon'
