import React, { FC } from 'react'
import { Typography } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import useStyles from './InfoPopup.styles'
import { IInfoPopup } from '../../../../services/types/vacacies-page-types/vacancies-page-types'
import { useAppDispatch, useAppSelector } from '../../../../services/store/hooks'
import { setProjectToClose, setProjectToDraft } from '../../../../services/slices/workshop-projects-list-page-slice'
import { patchProjectStatus } from '../../../../services/axios/workshop-projects-list-page'

const InfoPopup: FC<IInfoPopup> = ({ title, subtitle }) => {
  const classes = useStyles()
  const dispatch = useAppDispatch()
  const { projectToClose, projectToDraft } = useAppSelector((store) => store.workshopProjectsListPage)

  const handleClose = () => {
    dispatch(setProjectToClose(null))
    dispatch(setProjectToDraft(null))
  }

  const onSubmit = async () => {
    if (projectToClose) {
      dispatch(patchProjectStatus({ projectId: projectToClose, status: 'Набор закрыт' }))
      dispatch(setProjectToClose(null))
    }
    if (projectToDraft) {
      dispatch(patchProjectStatus({ projectId: projectToDraft, status: 'Черновик' }))
      dispatch(setProjectToDraft(null))
    }
  }

  return (
    <div>
      <Dialog classes={{ paper: classes.root }} open={!!projectToClose || !!projectToDraft} onClose={handleClose}>
        <div className={classes.container}>
          <DialogContent className={classes.dialogContent}>
            <form className={classes.form} id="form-consult">
              <div className={classes.titleContainer}>
                <Typography variant="h2" id="form-dialog-title" className={classes.title}>
                  {title}
                </Typography>
                <Typography className={classes.subtitle}>{subtitle}</Typography>
              </div>
            </form>
            <div className={classes.buttonContainer}>
              <Button type="button" className={classes.previousButton} onClick={handleClose}>
                Нет
              </Button>
              <Button className={classes.submitButton} type="button" onClick={onSubmit}>
                Да
              </Button>
            </div>
          </DialogContent>
        </div>
      </Dialog>
    </div>
  )
}

export default React.memo(InfoPopup)
