import React, { FC, useEffect, useState } from 'react'
import qs from 'qs'
import { GridCellParams } from '@mui/x-data-grid'
import { useNavigate } from 'react-router-dom'
import DataTable from '../../../components/Sub/DataTable/DataTableWithServerPagination'
import Filters from '../../../components/Sub/Filters/Filters'
import { getProjectsColumns } from './columns'
import useFilters from '../../../hooks/useFilters'
import useStyles from './WorkshopProjectsListPage.style'
import { useAppDispatch, useAppSelector } from '../../../services/store/hooks'
import {
  getDirectories,
  getProjectsList,
  patchProjectStatus,
} from '../../../services/axios/workshop-projects-list-page'
import { setRole } from '../../../services/slices/auth-slice'
import {
  TProjectStatusName,
  IParams,
  IProjectPageList,
} from '../../../services/types/workshop-projects-list-page-types/workshop-projects-list-page-types'
import InfoPopup from './InfoPopup/InfoPopup'
import {
  resetDeleteStatus,
  resetUpdateStatus,
  setIsLockUpdate,
  setProjectToClose,
  setProjectToDraft,
  setProjectToEdit,
} from '../../../services/slices/workshop-projects-list-page-slice'
import { resetCreateStatus, resetState } from '../../../services/slices/project-creation-slice'

// const sortFieldsMapping = {
//   id: 'id',
//   company_name: 'company_name',
//   position: 'position',
//   start_date: 'start_date',
//   end_date: 'end_date',
//   vacancy_status: 'vacancy_status_id',
// }

const WorkshopProjectsListPage: FC<IProjectPageList> = ({ setSnack }) => {
  const classes = useStyles()
  const hook = useFilters()
  const dispatch = useAppDispatch()
  const navigate = useNavigate()
  const { filtersJSON, projects, isLoading, rowsCount, projectToClose, updateStatus, isLockUpdate, deleteStatus } =
    useAppSelector((store) => store.workshopProjectsListPage)
  const { createStatus } = useAppSelector((store) => store.projectCreationPage)

  const paramsSerializer = (params: IParams) => qs.stringify(params, { arrayFormat: 'repeat' })

  const getProjectsListOnPage = async () => {
    const data = { hook, paramsSerializer }
    try {
      await dispatch(getProjectsList(data))
    } catch {
      return undefined
    }
    return undefined
  }

  const getDirectoriesOnPage = async () => {
    try {
      await dispatch(getDirectories())
    } catch {
      return undefined
    }
    return undefined
  }

  const patchVacancyStatusOnPage = async (projectId: number, status: { status: TProjectStatusName }) => {
    const patchData = { projectId, ...status }
    switch (status.status) {
      case 'Черновик':
        dispatch(setProjectToDraft(projectId))
        break
      case 'Набор закрыт':
        dispatch(setProjectToClose(projectId))
        break
      default:
        dispatch(patchProjectStatus(patchData))
        break
    }
  }

  const patchStatus = (projectID: number, status: { status: TProjectStatusName }) => {
    patchVacancyStatusOnPage(projectID, status)
  }

  const handleVacancyClick = (params: GridCellParams) => {
    dispatch(setProjectToEdit(params?.row))
    navigate('/workshop-managers/about-project', { state: { project: true } })
  }

  const columns = getProjectsColumns(patchStatus, filtersJSON.projects_statuses, () => null)

  useEffect(() => {
    if (isLockUpdate) {
      setTimeout(() => dispatch(setIsLockUpdate(false)), 10)
      dispatch(setProjectToEdit(null))
    }
    getDirectoriesOnPage()
    dispatch(setRole('workshop_manager'))
  }, [])

  useEffect(() => {
    // if (hook.filters.start_date && !isLockUpdate) getProjectsListOnPage()
    if (!isLockUpdate) getProjectsListOnPage()
  }, [hook.filters])

  useEffect(() => {
    if (updateStatus === 'ok') {
      setSnack({
        open: true,
        header: 'Статус изменён',
        message: 'Обновления вступят в силу через несколько секунд',
      })
      dispatch(resetUpdateStatus())
    }
  }, [updateStatus])

  useEffect(() => {
    if (deleteStatus === 'ok') {
      setSnack({
        open: true,
        header: 'Проект успешно удалён',
        message: 'Обновления вступят в силу через несколько секунд',
      })
      dispatch(resetDeleteStatus())
    }
  }, [deleteStatus])

  useEffect(() => {
    if (createStatus === 'ok') {
      setSnack({
        open: true,
        header: 'Проект успешно создан',
        message: 'Обновления вступят в силу через несколько секунд',
      })
      // скидываем весь слайс
      dispatch(resetState())
      setTimeout(() => dispatch(setIsLockUpdate(false)), 10)
    }
  }, [createStatus])

  return (
    <main className={classes.root}>
      <InfoPopup
        title={projectToClose ? 'Вы хотите закрыть набор?' : 'Перевести проект в статус Черновик?'}
        subtitle={
          projectToClose
            ? 'При смене статуса все открытые позиции внутри проекта автоматически закроются и перестанут быть видимыми для студента'
            : 'При смене статуса все открытые позиции внутри проекта автоматически закроются и перестанут быть видимыми для студента'
        }
      />
      <Filters
        filterType="ProjectsList"
        hook={hook}
        filtersJSON={
          filtersJSON?.projects_statuses
            ? { ...filtersJSON, statuses: [...filtersJSON?.projects_statuses] }
            : filtersJSON
        }
        handleAddVacancy={undefined}
        handleDownload={undefined}
        handleFeedExport={undefined}
        handleFunnelExport={undefined}
        handleAddCohort={undefined}
        handleAddContact={undefined}
        contactsCount={undefined}
        isStatistics={undefined}
      />
      <DataTable
        onCellClick={handleVacancyClick}
        sx={{
          '& .MuiDataGrid-cell': { cursor: 'pointer' },
        }}
        columns={columns}
        rows={projects}
        loading={isLoading}
        filtersHook={hook}
        rowsCount={rowsCount}
        sortFieldsMapping={undefined} // sortFieldsMapping={sortFieldsMapping}
      />
    </main>
  )
}

export default React.memo(WorkshopProjectsListPage)
